import React, { useState, useEffect } from 'react';
import { Line } from 'react-chartjs-2';
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend,
  ChartOptions,
  ChartData
} from 'chart.js';
import { collection, query, where, getDocs, orderBy } from 'firebase/firestore';
import { db } from '../../firebase/firebaseConfig';
import { useAuth } from '../../contexts/AuthContext';
import Button from '../common/Button';
import Card from '../common/Card';
import { ExercisePart, Workout } from '../../types';
import LoadingSpinner from '../common/LoadingSpinner';
import { X } from 'lucide-react';
import Badge from '../common/Badge';
//   import
import { getPartLabel, getPartColor, formatShortDate } from '../../utils/workoutUtils';

// Chart.js  
ChartJS.register(
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend
);

//   
const partOptions = [
  { value: 'chest', label: '' },
  { value: 'back', label: '' },
  { value: 'shoulder', label: '' },
  { value: 'leg', label: '' },
  { value: 'biceps', label: '' },
  { value: 'triceps', label: '' }
];

//    ( )
const exerciseOptions: Record<string, { value: string; label: string }[]> = {
  chest: [
    { value: 'benchPress', label: ' ' },
    { value: 'inclineBenchPress', label: '  ' },
    { value: 'declineBenchPress', label: '  ' },
    { value: 'cableFly', label: ' ' },
    { value: 'dumbbellFly', label: ' ' }
  ],
  back: [
    { value: 'deadlift', label: '' },
    { value: 'barbellRow', label: '' },
    { value: 'tBarRow', label: '' },
    { value: 'latPulldown', label: ' ' },
    { value: 'seatedRow', label: ' ' },
    { value: 'pullUp', label: '' }
  ],
  shoulder: [
    { value: 'overheadPress', label: ' ' },
    { value: 'lateralRaise', label: ' ' },
    { value: 'frontRaise', label: ' ' },
    { value: 'facePull', label: ' ' },
    { value: 'reverseFly', label: ' ' }
  ],
  leg: [
    { value: 'squat', label: '' },
    { value: 'legPress', label: ' ' },
    { value: 'lunges', label: '' },
    { value: 'legExtension', label: ' ' },
    { value: 'legCurl', label: ' ' }
  ],
  biceps: [
    { value: 'dumbbellCurl', label: ' ' },
    { value: 'barbellCurl', label: ' ' },
    { value: 'hammerCurl', label: ' ' }
  ],
  triceps: [
    { value: 'cablePushdown', label: ' ' },
    { value: 'overheadExtension', label: ' ' },
    { value: 'lyingTricepsExtension', label: '  ' }
  ]
};

//  
const periodOptions = [
  { value: '1month', label: '1' },
  { value: '3months', label: '3' },
  { value: '6months', label: '6' },
  { value: '1year', label: '1' }
];

//   
const setConfigOptions = [
  { value: 'all', label: '' },
  { value: '10x5', label: '10 x 5' },
  { value: '15x5', label: '15 x 5' },
  { value: '6x3', label: '6 x 3' },
  { value: 'custom', label: '' }
];

const WorkoutGraph: React.FC = () => {
  const { currentUser } = useAuth();
  const [selectedPart, setSelectedPart] = useState<string>('chest');
  const [selectedExercise, setSelectedExercise] = useState<string>('benchPress');
  const [selectedPeriod, setSelectedPeriod] = useState<string>('1month');
  const [selectedSetConfig, setSelectedSetConfig] = useState<string>('all');
  const [loading, setLoading] = useState<boolean>(true);
  const [error, setError] = useState<string | null>(null);
  
  //    
  const [workoutData, setWorkoutData] = useState<Workout[]>([]);
  const [filteredData, setFilteredData] = useState<Workout[]>([]);
  
  //     
  const [selectedWorkout, setSelectedWorkout] = useState<Workout | null>(null);
  const [dateWorkoutMap, setDateWorkoutMap] = useState<Record<string, Workout>>({});
  
  //  
  const [chartData, setChartData] = useState<ChartData<'line'>>({
    labels: [],
    datasets: []
  });
  
  //  
  const chartOptions: ChartOptions<'line'> = {
    responsive: true,
    plugins: {
      legend: {
        display: true,
        position: 'top',
        labels: {
          usePointStyle: true,
          padding: 15
        }
      },
      tooltip: {
        mode: 'index',
        intersect: false,
        callbacks: {
          title: (tooltipItems) => {
            return tooltipItems[0].label || '';
          },
          label: (context) => {
            const label = context.dataset.label || '';
            const value = context.raw as number;
            
            if (value === null) return null;
            
            let tooltipText = `${label}: ${value} kg`;
            
            // :         
            const dateLabel = context.chart.data.labels?.[context.dataIndex] as string;
            if (dateLabel && dateWorkoutMap[dateLabel]) {
              const workout = dateWorkoutMap[dateLabel];
              
              //    
              const sets = workout.mainExercise?.sets;
              if (sets && sets.length > 0) {
                tooltipText += `\n : ${sets.length} x ${sets[0].reps}`;
              }
              
              //    
              if (workout.accessoryExercises && workout.accessoryExercises.length > 0) {
                tooltipText += '\n :';
                workout.accessoryExercises.forEach(exercise => {
                  tooltipText += `\n- ${exercise.name}`;
                });
              }
            }
            
            return tooltipText;
          }
        }
      },
    },
    scales: {
      y: {
        beginAtZero: false,
        title: {
          display: true,
          text: ' (kg)'
        }
      },
      x: {
        title: {
          display: true,
          text: ''
        }
      }
    },
    interaction: {
      mode: 'nearest',
      axis: 'x',
      intersect: false
    },
    onClick: (event, elements, chart) => {
      if (elements.length === 0) return;
      
      const clickedElement = elements[0];
      const dataIndex = clickedElement.index;
      const label = chart.data.labels?.[dataIndex] as string;
      
      if (label && dateWorkoutMap[label]) {
        setSelectedWorkout(dateWorkoutMap[label]);
      }
    }
  };
  
  //   
  const prepareChartData = (data: Workout[]) => {
    try {
      if (!data || data.length === 0) {
        setChartData({
          labels: [],
          datasets: []
        });
        setDateWorkoutMap({});
        return;
      }
      
      //     
      const newDateWorkoutMap: Record<string, Workout> = {};
      
      //     
      const setConfigData: Record<string, Record<string, number>> = {
        '6x3': {},  // 6 3
        '10x5': {}, // 10 5
        '15x5': {}, // 15 5
        'custom': {} //   
      };
      
      //   
      const allDates: string[] = [];
      
      data.forEach(workout => {
        if (!workout.mainExercise || !workout.mainExercise.sets || workout.mainExercise.sets.length === 0) return;
        
        const dateStr = formatShortDate(new Date(workout.date));
        allDates.push(dateStr);
        
        //     ( )
        if (!newDateWorkoutMap[dateStr] || new Date(workout.date) > new Date(newDateWorkoutMap[dateStr].date)) {
          newDateWorkoutMap[dateStr] = workout;
        }
        
        //   
        const sets = workout.mainExercise.sets;
        let setConfig = 'custom';
        
        if (sets.length === 3 && sets.every(set => set.reps === 6)) {
          setConfig = '6x3';
        } else if (sets.length === 5 && sets.every(set => set.reps === 10)) {
          setConfig = '10x5';
        } else if (sets.length === 5 && sets.every(set => set.reps === 15)) {
          setConfig = '15x5';
        }
        
        //      
        let maxWeight = 0;
        sets.forEach(set => {
          if (set.isSuccess && set.weight > maxWeight) {
            maxWeight = set.weight;
          }
        });
        
        //    
        if (maxWeight > 0) {
          setConfigData[setConfig][dateStr] = maxWeight;
        }
      });
      
      //     
      setDateWorkoutMap(newDateWorkoutMap);
      
      //      
      const uniqueDates = [...new Set(allDates)].sort((a, b) => {
        const dateA = new Date(a);
        const dateB = new Date(b);
        return dateA.getTime() - dateB.getTime();
      });
      
      //    
      const datasets = [
        {
          label: '6x3',
          data: uniqueDates.map(date => setConfigData['6x3'][date] || null),
          borderColor: 'rgb(59, 130, 246)', // 
          backgroundColor: 'rgba(59, 130, 246, 0.5)',
          tension: 0.2,
          pointRadius: 5,
          pointBackgroundColor: uniqueDates.map(date => 
            setConfigData['6x3'][date] ? 'rgb(59, 130, 246)' : 'transparent'
          ),
          pointBorderColor: uniqueDates.map(date => 
            setConfigData['6x3'][date] ? 'rgb(59, 130, 246)' : 'transparent'
          ),
          pointHoverRadius: 8,
          pointHoverBackgroundColor: 'rgba(59, 130, 246, 0.9)',
          pointHitRadius: 10,
          spanGaps: false //      
        },
        {
          label: '10x5',
          data: uniqueDates.map(date => setConfigData['10x5'][date] || null),
          borderColor: 'rgb(239, 68, 68)', // 
          backgroundColor: 'rgba(239, 68, 68, 0.5)',
          tension: 0.2,
          pointRadius: 5,
          pointBackgroundColor: uniqueDates.map(date => 
            setConfigData['10x5'][date] ? 'rgb(239, 68, 68)' : 'transparent'
          ),
          pointBorderColor: uniqueDates.map(date => 
            setConfigData['10x5'][date] ? 'rgb(239, 68, 68)' : 'transparent'
          ),
          pointHoverRadius: 8,
          pointHoverBackgroundColor: 'rgba(239, 68, 68, 0.9)',
          pointHitRadius: 10,
          spanGaps: false
        },
        {
          label: '15x5',
          data: uniqueDates.map(date => setConfigData['15x5'][date] || null),
          borderColor: 'rgb(16, 185, 129)', // 
          backgroundColor: 'rgba(16, 185, 129, 0.5)',
          tension: 0.2,
          pointRadius: 5,
          pointBackgroundColor: uniqueDates.map(date => 
            setConfigData['15x5'][date] ? 'rgb(16, 185, 129)' : 'transparent'
          ),
          pointBorderColor: uniqueDates.map(date => 
            setConfigData['15x5'][date] ? 'rgb(16, 185, 129)' : 'transparent'
          ),
          pointHoverRadius: 8,
          pointHoverBackgroundColor: 'rgba(16, 185, 129, 0.9)',
          pointHitRadius: 10,
          spanGaps: false
        },
        {
          label: ' ',
          data: uniqueDates.map(date => setConfigData['custom'][date] || null),
          borderColor: 'rgb(156, 163, 175)', // 
          backgroundColor: 'rgba(156, 163, 175, 0.5)',
          tension: 0.2,
          pointRadius: 5,
          pointBackgroundColor: uniqueDates.map(date => 
            setConfigData['custom'][date] ? 'rgb(156, 163, 175)' : 'transparent'
          ),
          pointBorderColor: uniqueDates.map(date => 
            setConfigData['custom'][date] ? 'rgb(156, 163, 175)' : 'transparent'
          ),
          pointHoverRadius: 8,
          pointHoverBackgroundColor: 'rgba(156, 163, 175, 0.9)',
          pointHitRadius: 10,
          spanGaps: false
        }
      ];
      
      setChartData({
        labels: uniqueDates,
        datasets: datasets
      });
    } catch (error) {
      console.error('   :', error);
    }
  };
  
  // Firestore  
  useEffect(() => {
    const fetchWorkoutData = async () => {
      if (!currentUser) {
        setError(' ');
        setLoading(false);
        return;
      }
      
      try {
        setLoading(true);
        
        //     
        const endDate = new Date();
        let startDate = new Date();
        
        switch (selectedPeriod) {
          case '1month':
            startDate.setMonth(startDate.getMonth() - 1);
            break;
          case '3months':
            startDate.setMonth(startDate.getMonth() - 3);
            break;
          case '6months':
            startDate.setMonth(startDate.getMonth() - 6);
            break;
          case '1year':
            startDate.setFullYear(startDate.getFullYear() - 1);
            break;
          default:
            startDate.setMonth(startDate.getMonth() - 1);
        }
        
        // Firestore  
        const sessionsCollection = collection(db, 'sessions');
        const q = query(
          sessionsCollection,
          where('userId', '==', currentUser.uid),
          where('date', '>=', startDate),
          where('date', '<=', endDate),
          orderBy('date', 'asc')
        );
        
        const querySnapshot = await getDocs(q);
        
        //   
        const data = querySnapshot.docs.map(doc => ({
          id: doc.id,
          ...doc.data(),
          date: new Date(doc.data().date.toDate()),
        })) as Workout[];
        
        setWorkoutData(data);
        //   
        applyFilters(data);
        
      } catch (error) {
        console.error('   :', error);
        setError('     .');
      } finally {
        setLoading(false);
      }
    };
    
    fetchWorkoutData();
  }, [currentUser, selectedPeriod]);
  
  //  
  const applyFilters = (data = workoutData) => {
    try {
      let filtered = [...data];
      
      //  
      filtered = filtered.filter(item => item.part === selectedPart);
      
      //   
      if (selectedExercise !== 'all') {
        filtered = filtered.filter(item => 
          item.mainExercise && item.mainExercise.name.includes(
            exerciseOptions[selectedPart].find(opt => opt.value === selectedExercise)?.label || ''
          )
        );
      }
      
      //    (    )
      if (selectedSetConfig !== 'all') {
        filtered = filtered.filter(item => {
          if (!item.mainExercise || !item.mainExercise.sets || item.mainExercise.sets.length === 0) {
            return false;
          }
          
          //     
          const sets = item.mainExercise.sets;
          
          if (selectedSetConfig === '10x5') {
            return sets.length === 5 && sets.every(set => set.reps === 10);
          } else if (selectedSetConfig === '15x5') {
            return sets.length === 5 && sets.every(set => set.reps === 15);
          } else if (selectedSetConfig === '6x3') {
            return sets.length === 3 && sets.every(set => set.reps === 6);
          } else if (selectedSetConfig === 'custom') {
            // 10x5, 15x5, 6x3     
            return !(
              (sets.length === 5 && sets.every(set => set.reps === 10)) ||
              (sets.length === 5 && sets.every(set => set.reps === 15)) ||
              (sets.length === 3 && sets.every(set => set.reps === 6))
            );
          }
          
          return true;
        });
      }
      
      setFilteredData(filtered);
      
      //   
      prepareChartData(filtered);
    } catch (error) {
      console.error('  :', error);
    }
  };
  
  //     
  useEffect(() => {
    applyFilters();
  }, [selectedPart, selectedExercise, selectedSetConfig]);
  
  //   
  const handlePartChange = (e: React.ChangeEvent<HTMLSelectElement>) => {
    const part = e.target.value;
    setSelectedPart(part);
    //      
    setSelectedExercise('all');
  };
  
  if (loading) {
    return (
      <div className="flex justify-center items-center h-64">
        <LoadingSpinner />
      </div>
    );
  }
  
  if (error) {
    return (
      <div className="bg-red-100 border border-red-400 text-red-700 px-4 py-3 rounded">
        <p>{error}</p>
      </div>
    );
  }

  return (
    <div className="space-y-6">
      {/*       */}
      <Card className="animate-slideUp">
        <h3 className="text-lg font-semibold mb-4">  </h3>
        
        {/*     */}
        <div className="mb-6">
          <div className="flex items-center flex-wrap gap-2 p-1 bg-gray-100 dark:bg-gray-700 rounded-lg">
            {partOptions.map(option => (
              <button
                key={option.value}
                type="button"
                onClick={() => setSelectedPart(option.value)}
                className={`
                  py-2 px-4 rounded-lg flex items-center transition-all duration-300 text-sm font-medium
                  ${selectedPart === option.value 
                    ? 'bg-emerald-500 text-white shadow-lg transform scale-105'
                    : 'bg-gray-100 dark:bg-gray-700 text-gray-700 dark:text-gray-300 hover:bg-gray-200 dark:hover:bg-gray-600'
                  }
                `}
              >
                {option.label}
              </button>
            ))}
          </div>
        </div>
        
        <div className="flex flex-col lg:flex-row gap-4">
          {/* :  */}
          <div className="flex-1">
            {filteredData.length > 0 ? (
              <div className="h-120">
                <Line options={chartOptions} data={chartData} />
              </div>
            ) : (
              <div className="h-64 flex items-center justify-center">
                <p className="text-gray-500 dark:text-gray-400">
                  {`${partOptions.find(p => p.value === selectedPart)?.label || ''}    .`}
                </p>
              </div>
            )}
          </div>
          
          {/* :     */}
          <div className="lg:w-2/5 border-t lg:border-t-0 lg:border-l border-gray-200 dark:border-gray-700 pt-4 lg:pt-0 lg:pl-4">
            {selectedWorkout ? (
              <div className="animate-fadeIn">
                <div className="flex justify-between items-center mb-3">
                  <h4 className="font-medium">   </h4>
                  <Button 
                    size="sm" 
                    className="bg-transparent text-gray-500 hover:text-gray-700 p-1"
                    onClick={() => setSelectedWorkout(null)}
                    icon={<X size={18} />}
                  />
                </div>
                <div className="space-y-3">
                  <div className="flex flex-wrap gap-2">
                    <Badge variant="primary">
                      {typeof selectedWorkout.date === 'string' 
                        ? new Date(selectedWorkout.date).toLocaleDateString() 
                        : selectedWorkout.date.toLocaleDateString()}
                    </Badge>
                    <Badge variant="secondary">{getPartLabel(selectedWorkout.part)}</Badge>
                    <Badge variant={selectedWorkout.isAllSuccess ? 'success' : 'danger'}>
                      {selectedWorkout.isAllSuccess ? '' : ' '}
                    </Badge>
                  </div>
                  <div>
                    <h4 className="font-medium">{selectedWorkout.mainExercise?.name}</h4>
                    <div className="grid grid-cols-1 sm:grid-cols-2 gap-2 mt-2">
                      {selectedWorkout.mainExercise?.sets?.map((set, index) => (
                        <div 
                          key={index}
                          className={`p-2 rounded-lg text-sm ${
                            set.isSuccess 
                              ? 'bg-green-50 dark:bg-green-900/30 border-l-2 border-green-500' 
                              : 'bg-red-50 dark:bg-red-900/30 border-l-2 border-red-500'
                          }`}
                        >
                          <div className="flex justify-between items-center">
                            <span className="font-medium"> {index + 1}</span>
                            <Badge 
                              variant={set.isSuccess ? 'success' : 'danger'} 
                              size="sm"
                            >
                              {set.isSuccess ? '' : ''}
                            </Badge>
                          </div>
                          <div className="mt-1 grid grid-cols-2 gap-1">
                            <div>
                              <span className="text-xs text-gray-500 dark:text-gray-400"></span>
                              <p className="font-bold">{set.weight} kg</p>
                            </div>
                            <div>
                              <span className="text-xs text-gray-500 dark:text-gray-400"></span>
                              <p className="font-bold">{set.reps} </p>
                            </div>
                          </div>
                        </div>
                      ))}
                    </div>
                  </div>
                  
                  {/*     */}
                  {selectedWorkout.accessoryExercises && selectedWorkout.accessoryExercises.length > 0 && (
                    <div>
                      <h4 className="font-medium text-sm mt-3"> </h4>
                      <div className="grid grid-cols-1 gap-2 mt-2">
                        {selectedWorkout.accessoryExercises.map((exercise, index) => (
                          <div 
                            key={index}
                            className="p-2 bg-gray-50 dark:bg-gray-800 rounded-lg text-sm"
                          >
                            <p className="font-medium">{exercise.name}</p>
                            {exercise.sets && exercise.sets.length > 0 ? (
                              <div className="grid grid-cols-2 gap-2 mt-1">
                                {exercise.sets.map((set, setIndex) => (
                                  <div key={setIndex} className="text-xs flex justify-between">
                                    <span> {setIndex + 1}:</span>
                                    <span>{set.weight}kg  {set.reps}</span>
                                  </div>
                                ))}
                              </div>
                            ) : (
                              <div className="mt-1 text-xs flex justify-between">
                                {exercise.weight && <span>: {exercise.weight}kg</span>}
                                {exercise.reps && <span>: {exercise.reps}</span>}
                              </div>
                            )}
                          </div>
                        ))}
                      </div>
                    </div>
                  )}
                  
                  {selectedWorkout.notes && (
                    <div>
                      <h4 className="font-medium text-sm"></h4>
                      <p className="p-2 bg-gray-50 dark:bg-gray-800 rounded-lg mt-1 text-sm">{selectedWorkout.notes}</p>
                    </div>
                  )}
                </div>
              </div>
            ) : (
              <div className="h-full flex items-center justify-center text-center p-6">
                <div className="text-gray-500 dark:text-gray-400">
                  <p className="mb-2">   </p>
                  <p>      .</p>
                </div>
              </div>
            )}
          </div>
        </div>
      </Card>
    </div>
  );
};

export default WorkoutGraph; 